// Define your custom mixins here.
// -----------------------------------------------------------------------------

@mixin link($color, $hColor) {
  color: $color;
  // font-size: 18px;

  &:hover {
    color: $hColor;
    text-decoration: underline;
  }
}

@mixin button {
  border: 1px solid $button-border-color;
  border-radius: 5px;
  color: $nav-link-color;
  display: inline-block;
  font-size: 11px;
  font-weight: 600;
  line-height: 20px;
  margin: 1rem 0;
  text-align: center;
  transition: all 0.3s ease-in-out;
  

  a {
    display: block;
    text-decoration: none;
    border-radius: 5px;
    
    &::after{
      content: '>';
      display: inline-block;
      font-size: 11px;
      margin-left: 20px;
      position: relative;
    }
    &:hover {
      color: $black;
      background-color: $white; 
  
      &::after {
        color: $black;
      }
    }
  }
}
